.articles-electromenager{
    margin: 15px;
    border: solid 4.5px $color-2;
    border-radius: 12px;
    &:hover{
        h3{
            visibility: visible !important;
            color:  tomato;
            transform: translateX(25px);
            transition: 0.5s;
        }
    }

    .prix{
        display: flex;
        justify-content: space-around;

        button{
            padding: 15px;
            background-color: teal;
            border: solid 3px teal;
            border-radius: 8px;
            color: $color-1;
            font-size: 1.2rem;
            font-family: $font1;

            &:hover{
                color: rgb(247, 168, 154);
                transition: 0.5s;
            }
        }
    }
}