.article-mobilier {
    padding: 5px 0;
    margin: 25px 0;
    border: solid 3.5px paleturquoise;
    border-radius: 11px;
    &:hover{
        h3{
            visibility: visible !important;
            color:  tomato;
            transform: translateX(25px);
            transition: 0.5s;
        }
    }

    img {
        width: 90%;
    }

    .prix {
        display: flex;
        justify-content: space-around;

        button {
            background-color: azure;
            border: solid aqua 2px;
            border-radius: 12px;
            color: darkblue;
            ;
            font-family: $font1;
            font-size: 1.2rem;
            padding: 1rem;
            cursor: pointer;

            &:hover {
                color: tomato;
                transition: 0.5s;
            }
        }
    }
}