.navigation {
  margin: 2rem 0 1.2rem 0;
  display: grid;
  grid-template-columns: 70% 30%;

  .active-page {
    color: $color-2;
    text-decoration: underline 5px;
    font-family: $font1;
  }

  .pages {
    ul {
      display: flex;
      justify-content: space-around;
      border: solid 10px $color-1;
      border-radius: 15px;
      padding: 18px 0;
      margin-left: 15px;
    }
  }

  .panier {
    padding: 18px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 25%;

    img {
      margin-left: 55px;
      width: 25%;
      border-radius: 15px;
    }

    input[type="text"] {
      text-align: center;
      border-radius: 5px;
      padding: 10px 0;
      width: 80%;
    }
  }
}

/***********************RESPONSIVE*********************/
@media screen and (max-width: 860px) {
  .navigation {
    .pages {
      ul {
        font-size: 0.7em;
      }
    }

    .panier {
      margin-left: 0;

      img {
        width: 20%;
      }

      input[type="text"] {
        border-radius: 10px;
        width: 80%;
      }
    }
  }
}

@media screen and (max-width: 670px) {
  .navigation {
    display: grid;
    grid-template-columns: 35% 65%;

    .pages {
      ul {
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
      }

      li {
        margin: 10px;
        font-size: 1.2em;
        padding: 5px;
      }
    }
  }
}

@media screen and (max-width: 540px) {
  .navigation {
    .pages {
      ul {
        border: none;
        margin-left: 1.5rem;
      }
    }
  }
}
