.electronique {
    .img-accueil {
        margin: 0 auto;
        width: 95%;

        img {
            margin: 0 auto;
            width: 100%;
            height: 35vh;
            padding: 20px 0;
            border: solid 1.5px $color-5;
            border-radius: 25px;
        }
    }

    .electronique-articles {
        ul {
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
        }
    }
}