.electromenager {
    .image-accueil {
        margin: 0 auto;
        width: 95%;

        img {
            margin: 0 auto;
            width: 95%;
            height: 45vh;
            border-radius: 25px;
        }
    }

    .liste-electromenager {
        margin: 50px 0;

        ul {
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
        }

        img {
            width: 75%;
        }
    }
}