.home {
  .img-home {
    img {
      height: 35vh;
      width: 100%;
      border-radius: 30px;
    }
  }

  .img-nav {
    margin: 25px 0;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    h2 {
      text-decoration: overline $color-1 8px;
      font-family: $font2;
      padding: 10px 0;
    }

    img {
      width: 75%;
      border: solid 5px whitesmoke;
      border-radius: 15px;

      &:hover {
        transform: rotate(10deg);
        width: 90%;
        transition: 0.8s;
      }
    }
  }

  footer {
    background-color: khaki;
    padding: 25px 0;

    h2 {
      font-family: $font1;
      font-size: 2.5rem;
      text-align: center;
    }

    form {
      margin: 0 auto;
      max-width: 900px;
      padding: 1.2rem 0;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr;
    }

    input[type="text"],
    input[type="email"] {
      border: solid 2px whitesmoke;
      border-radius: 15px;
      font-size: 1.5rem;
      margin: 0 auto;
      width: 35%;
      height: 8vh;
      user-select: none;
    }

    textarea {
      margin: 0 auto;
      width: 45%;
      height: 14vh;
      font-size: 1.5rem;
      border: solid 2px whitesmoke;
      border-radius: 11px;
      resize: none;
      user-select: none;
    }

    input[type="submit"] {
      margin: 0 auto;
      width: 25%;
      height: 5vh;
      border: solid 2px rgb(235, 155, 141);
      border-radius: 10px;
      background-color: rgb(235, 155, 141);
      color: $color-4;
      font-family: $font1;
      font-size: 1.2rem;
      font-weight: 500;
      cursor: pointer;

      &:hover {
        width: 30%;
        //height: 7vh;
        transform: scale(1.1);
        color: white;
        transition: 0.5s;
        font-weight: 800;
      }
    }

    #bar {
      margin: 0 auto;
      border: solid rgb(150, 241, 241) 1px;
      border-radius: 12px;
      width: 75%;
    }

    .social {
      margin: 0 auto;
      width: 50%;
      margin-top: 2rem;

      .social-link {
        margin-top: 2rem;
        display: flex;
        justify-content: space-around;

        img {
          width: 75%;

          &:hover {
            transform: scale(1.2);
            transition: 0.5s;
          }
        }
      }
    }

    span {
      position: relative;

      &:after {
        position: absolute;
        content: "\00A9";
        font-size: 2rem;
        right: -22px;
        top: -11px;
      }
    }
  }
}

//******************************** RESPONSIVE ****************************//
@media screen and (max-width: 640px) {
  .home {
    footer {
      h2 {
        font-size: 1.5rem;
      }
      form {
        input[type="text"],
        input[type="email"] {
          width: 85%;
          font-size: 1.1rem;
        }
        textarea {
          width: 75%;
          font-size: 1.1rem;
        }
      }
      .signature {
        padding: 0.5rem;
        letter-spacing: 5px;
      }
    }
  }
}
