.mobilier {
    .image-accueil {
        margin: 0 auto;
        width: 95%;

        img {
            margin: 0 auto;
            width: 100%;
            height: 35vh;
            border-radius: 18px;
        }
    }

    .liste-mobilier {
        ul {
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
        }

    }
}