.electronique-list {
    cursor: pointer;
    margin: 25px;
    padding: 15px 0;
    border: solid 2px $color-2;
    border-radius: 15px;

    &:hover {
        h3 {
            visibility: visible !important;
            color: tomato;
            font-size: 1.2rem;
            transform: translateX(25px);
            transition: 0.5s;
        }
    }

    img {
        width: 95%;
        height: 30vh;
    }

    .prix {
        display: flex;
        justify-content: space-around;

        button {
            background-color: $color-2;
            border: solid 2px $color-2;
            border-radius: 11px;
            font-family: $font1;
            color: $color-1;

            &:hover {
                background-color: $color-5;
            }
        }
    }
}