// @import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');

$color-1: #ffe6e6;
$color-2: #ff8080;
$color-3: #ff3333;
$color-4: #ffffff;
$color-5: rgba(6, 6, 65, 0.993);

$font1: Nova Round;
$font2: Dosis;
$font3: Josephin Sans;

html {
    scroll-behavior: smooth;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0.4rem auto;
    max-width: 1296px;
    color: $color-5;
    background-color: $color-4;
    font-family: $font2;
    font-weight: 700;
}

li {
    list-style-type: none;
    color: $color-4;
}

a {
    text-decoration: none;
    color: $color-5;
    cursor: pointer;
}

a:visited {
    color: none;
}