.liste-habillement {
    margin: 25px 0;
    padding: 15px 0;
    cursor: pointer;

    &:hover {

        h3 {
            visibility: visible !important;
            color: tomato;
            font-family: $font2;
            transform: translateX(25px);
            transition: 0.5s;
        }
    }

    img {
        width: 90%;
        height: 30vh;
        // padding: 1.2rem;
        border: solid 5px $color-2;
        border-radius: 15px;

        &:hover {
            transform: scale(1.2);
            transition: 0.5s;
            border: none;
        }
    }

    .prix {
        display: flex;
        justify-content: baseline;
        padding: 1.2rem;

        button {
            cursor: pointer;
            background-color: $color-5;
            color: white;
            border: solid 1px $color-3;
            border-radius: 5px;
            padding: 1rem;
            margin-left: 8px;
            font-family: $font1;
            font-size: 1.1rem;

            &:hover {
                color: $color-2;
                transition: 0.8s;
            }
        }
    }
}